/* GENERAL */
@font-face {
  font-family: 'National2WebRegular';
  src: url('../font/national2/National2Web-Regular.eot'); /* IE9 Compat Modes */
  src: url('../font/national2/National2Web-Bold.woff2') format('woff2'); /* Super Modern Browsers */
  src: url('../font/national2/National2Web-Regular.woff') format('woff'); /* Pretty Modern Browsers */
  font-weight: 400;
  font-style: normal;
}
body {
  font-family: 'National2WebRegular', serif;
}
p, a {
  font-style: italic;
  font-size: .9em;
  color: #8e8e8e;
  margin: .4em 0;
}
a:link, a:visited, a:active, a:hover {
  color: #8e8e8e;
}
span {
  font-weight: bold; 
}

/* class and id based */
/* MAP */
#map-container {
  max-width: 870px;
}
#map {
  max-width: 869px;
  height: 350px;
}
#hidden-div {
  display: none;
}
.marker {
  width: 9px;
  height: 9px;
  border: 1px solid black;
  border-radius: 20px;
  background-color: #3e3e3e;
  cursor: help;
}
.marker:hover {
  background-color: orange !important;
  border-color: white;
}
.perma-marker {
  width: 9px;
  height: 9px;
  border: 1px solid black;
  border-radius: 10px;
  background-color: red;
}
.marker-legend {
  width: 9px;
  height: 9px;
  border: 1px solid black;
  border-radius: 10px;
  background-color: #3e3e3e;
  margin: 5px 0;
}

#legend {
  margin: .1em 0;
}
#legend-h3 {
  background-color: #006699;
  color: white;
  padding: .5em;
  cursor: pointer;
  margin: .1em 0;
}
#legend-h3:hover {
  background-color: #6699cc;
}
#MZ-lightblue {
  background-color: #a7ccdc;
}
#MZ-midblue {
  background-color: #6699cc;
}
#MZ-darkblue {
  background-color: #006699;
}
#MZ-lightgray {
  background-color: #8e8e8e;
}
#MZ-darkgray {
  background-color: #3e3e3e;
}
#legend-inner-container {
  margin: .1em 0 1em .1em;
}

#info-row {
  margin: 0 0 .5em 0;
}
#info-row > p {
  margin: 0;
}
.legend-rows {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 1em;
}
.legend-rows > div {
  display: grid;
  grid-template-columns: 1fr 9fr;
  grid-gap: .5em;
}
.legend-p {
  margin: 0;
}

/* USER INTERACTION */
.hidden {
  display: none !important;
}
hr {
  max-width: 870px;
  margin: .5em 0;
}
#user-info {
  max-width: 870px;
  margin: .1em 0;
}
#user-info-h3 {
  background-color: #8e8e8e;
  color: white;
  padding: .5em;
  cursor: pointer;
  margin: .1em 0;
  /* added to solve scroll prob on safari with child-div #user-info-inner-container: START */
  -webkit-overflow-scrolling: touch;
  overflow-y: scroll;
  /* added to solve scroll prob on safari with child-div #user-info-inner-container: END */
}
#user-info-h3:hover {
  background-color: #3e3e3e;
}

/* POPUP */

.popup {
  width: 250px;
  padding: .5em 1em;
  height: auto;
  background-color: #006699;
  color: white;
  /* für ungefähr mittige Anordnung des Popup - prob bei großen Bildschirmen, weil nicht auf 780px-contaienr beschränkt */
  position: fixed;  
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  
}
.popup-row {
  display: grid; 
  grid-template-columns: 7fr 1fr;
}
.popup-title {
  margin: .3em 0;
}
.close {
  margin: auto 0;
  background-color: #3e3e3e;
  color: white;
  border: 1px solid white;
  text-align: center;
  cursor: pointer;
  font-style: inherit;
  font-weight: bold;
  font-size: 1.5em;
}
.close:hover {
  background-color: #8e8e8e;
  color: #3e3e3e !important;
}
.popup > p {
  color: white;
}
.adr {
  font-style: inherit;
}
.popup-link-row {
  display: grid;
  grid-template-columns: 2fr 2fr 2fr;
  grid-gap: .2em;
}
.popup-link {
  color: white !important;
  text-decoration: none;
  background-color: #3e3e3e;
  border: 1px solid white;
  padding: .1em .5em .3em .5em;
  text-align: center;
  cursor: pointer;
  font-style: inherit;
  font-weight: bold;
}
.popup-link:hover {
  background-color: #8e8e8e;
  color: #3e3e3e !important;
}
.popup-single-item {
  display: grid;
  grid-template-columns: 1fr;
}
.link-info {
  font-size: .7em;
  margin: 0;
}

/* MEDIA-QUERIES */
@media only screen and (max-width: 840px) {
  .legend-rows{
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: .5em;
  }
}

@media only screen and (max-width: 520px) {
  .legend-rows{
    grid-template-columns: 1fr 1fr;
    grid-gap: .1em;
  }
}
